const moment = require('moment');

const formatDate = (momentDate) => {
  const formatedMomentDate = momentDate.weekday(1).format('DD.MM.YYYY');
  const formatedMomentEndDate = momentDate.clone().weekday(7).format('DD.MM.YYYY');

  return `KW ${momentDate.format('WW')}: ${formatedMomentDate} - ${formatedMomentEndDate}`;
};

const proposedPrice = (form) => {
  let weekprice = 0;
  let resultPrice = 0;
  switch (form.format) {
    case 'Paket S':
      weekprice = 1600;
      break;
    case 'Paket M':
      weekprice = 3200;
      break;
    default: // '1/1 Seite'
      weekprice = 4000;
      break;
  }
  for (let step = 1; step <= form.dateCount; step += 1) {
    weekday = moment(form[`date${step}`]).weekday();
    resultPrice += weekprice;
  }
  return resultPrice;
};

const extractWeekdays = (date) => {
  let momentDate = moment(date);
  const extractedWeekdays = [];
  if (momentDate.weekday() !== 1) {
    momentDate = momentDate.weekday(1);
  }
  for (let count = 1; count <= 7; count += 1) {
    extractedWeekdays.push(momentDate.toDate());
    momentDate.add(1, 'd');
  }
  return extractedWeekdays;
};

const calculateDisabledDates = (form) => {
  let result = [];
  for (let step = 1; step <= form.dateCount; step += 1) {
    result = result.concat(extractWeekdays(form[`date${step}`]));
  }
  return result;
};

const findNextDate = (form) => {
  const disabledDates = calculateDisabledDates(form);
  const lastDate = disabledDates[disabledDates.length - 1];
  let nextDate = moment(lastDate).add(1, 'days').toDate();
  let nextDateAlreadyExists = false;
  let currentDate;
  do {
    nextDateAlreadyExists = false;
    for (let i = 0; i < disabledDates.length; i += 1) {
      currentDate = disabledDates[i];
      if (moment(currentDate).dayOfYear() === moment(nextDate).dayOfYear()) {
        nextDateAlreadyExists = true;
        nextDate = moment(nextDate).add(7, 'days').toDate();
      }
    }
  } while (nextDateAlreadyExists);
  return nextDate;
};

const extractFromData = (form, token) => {
  const dates = [];

  for (let step = 1; step <= form.dateCount; step += 1) {
    dates.push(formatDate(moment(form[`date${step}`])));
  }

  const formData = {
    firstname: form.fname,
    lastname: form.lname,
    email: form.email,
    phone: form.phone,
    company: form.company,
    grecaptchaToken: token,
    format: form.format,
    desiredPrice: form.desiredPrice,
    savedPrice: form.savedPrice,
    saveDiscount: form.saveDiscount,
    listingPrice: form.listingPrice,
    support: form.support,
    dates,
  };

  let landingpageform = `Format: ${form.format}\n`;
  landingpageform += `Wunschpreis: ${form.desiredPrice}€\n`;
  landingpageform
    += `Werbemittelerstellung gewünscht: ${form.support}\n`;
  for (let step = 1; step <= form.dateCount; step += 1) {
    landingpageform = `${landingpageform
      + formatDate(moment(form[`date${step}`]))
    }\n`;
  }
  formData.landingpageform = landingpageform;
  return formData;
};

const formatPrice = (price) => price.toString().replace(/(.)(?=(\d{3})+$)/g, '$1.');

module.exports = {
  calculateDisabledDates,
  extractFromData,
  extractWeekdays,
  findNextDate,
  formatDate,
  formatPrice,
  proposedPrice,
};
