DatePicker.locale('de', languageConfig);

const axios = require('axios');
// application/json is not supported by CORS
axios.defaults.headers.post['Content-Type'] = 'text/plain';
const moment = require('moment');

const helper = require('./formHelper.js');

moment().isoWeekday(1);
// next monday
let initialDate = moment().day(8).toDate();

if (moment().day() > 4) {
  // next next monday if today is thursday or later
  initialDate = moment().day(15).toDate();
}

export default {
  components: { DatePicker },
  data() {
    return {
      customFormatter: {
        stringify: (date) => {
          const momentDate = moment(date);
          return helper.formatDate(momentDate);
        },
      },
      form: {
        extended: false,
        isSending: false,
        isCompleted: false,
        isFailed: false,
        date1: initialDate,
        format: 'Paket S',
        fname: '',
        lname: '',
        phone: '',
        email: '',
        company: '',
        price: '',
        agbs: '',
        support: 'support-yes',
        dateCount: 1,
        desiredPrice: 0,
        savedPrice: 0,
        listingPrice: 0,
        saveDiscount: '',
      },
    };
  },
  methods: {
    submitForm() {
      if (this.form.isSending || this.form.isCompleted) {
        return;
      }
      this.form.isSending = true;
      load('6LfplLcUAAAAAEiTuzzpCYV-XUIBsFahbha-4AuP').then((recaptcha) => {
        recaptcha.execute('homepage').then((token) => {
          const formData = helper.extractFromData(this.form, token);

          axios
            .post('https://europe-west1-pryntad.cloudfunctions.net/Kecleon-createLead-lvz', JSON.stringify(formData))
            .then(() => {
              window.dataLayer.push({ event: 'conversion' });
              this.form.isCompleted = true;
            })
            .catch(() => {
              this.form.isFailed = true;
            })
            .finally(() => {
              this.form.isSending = false;
            });
        });
      });
    },
    removeLastDate() {
      this.form[`date${this.form.dateCount}`] = '';
      this.form.dateCount -= 1;
    },
    disabledDate(date) {
      if (moment(date).format('YYYYMMDD') < moment(initialDate).format('YYYYMMDD')) {
        return true;
      }

      const disabledDates = helper.calculateDisabledDates(this.form);

      for (let index = 0; index < disabledDates.length; index += 1) {
        if (moment(disabledDates[index]).format('YYYYMMDD') === moment(date).format('YYYYMMDD')) {
          return true;
        }
      }
      return false;
    },
    proposedPrice() {
      return helper.formatPrice(helper.proposedPrice(this.form));
    },
    touchDate1() {
      // this is a hack because the datepicker will not update the customFormatter
      // of the second input field, when updated
      this.form.date1 = moment(this.form.date1).add(1, 'days').toDate();
      this.form.date1 = moment(this.form.date1).add(-1, 'days').toDate();
    },
    openForm() {
      if (this.form.extended) {
        return;
      }
      this.form.extended = true;
      if (window.hj) {
        window.hj('trigger', process.env.HOTJAR_TRIGGER);
      }
    },
    savingPrice() {
      const proposedPrice = helper.proposedPrice(this.form);
      const saving = helper.formatPrice(proposedPrice - this.form.price);
      if (this.form.price < proposedPrice && this.form.price >= 100) {
        this.form.desiredPrice = this.form.price;
        this.form.listingPrice = proposedPrice;
        this.form.savedPrice = saving;
        this.form.saveDiscount = `${Math.floor(((proposedPrice - this.form.price) / proposedPrice) * 100)}%`;
        return saving;
      }
      return false;
    },
    discountPrice() {
      // save at least 20%
      const proposedPrice = helper.proposedPrice(this.form);
      const discount = proposedPrice / 5;
      return helper.formatPrice(proposedPrice - discount);
    },
    addDate() {
      this.form[`date${this.form.dateCount + 1}`] = helper.findNextDate(this.form);
      this.form.dateCount += 1;
    },
  },
};
</script>
